  .layout-wrapper-dark {
    --body-bg: #081325;
    --menu-bg: #081325;
    --demo-bg-blue: var(--blue-900);
    --demo-bg-yellow: var(--yellow-900);
    --demo-bg-green: var(--green-900);
    --demo-bg-purple: var(--purple-900);
    --demo-bg-indigo: var(--indigo-900);
    --demo-bg-orange: var(--orange-900);
    --demo-bg-cyan: var(--cyan-900);
    --menu-border-color: hsla(0, 0%, 100%, 0.1);
    --topbar-bg: rgba(8, 19, 37, 0.4);
    --menu-icon-hover-filter: invert(35%) sepia(100%) saturate(1296%) hue-rotate(80deg)
      brightness(215%) contrast(100%);
  }

  .layout-wrapper-light {
    --body-bg: #f6f7f9;
    --menu-bg: #f6f7f9;
    --demo-bg-blue: var(--blue-50);
    --demo-bg-yellow: var(--yellow-50);
