code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.p-inputtext {
  font-family: 'Fira Code', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

  &::placeholder {
    font-family: 'Fira Code', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: 1rem;
    color: inherit;
    opacity: 0.5;
  }
}

.border-50 {
  border-color: light-dark(var(--surface-500), var(--surface-800)) !important;
}

.p-dialog-content {
  padding: 0;
}

.p-toast {
  font-family: 'Inter', sans-serif;
}
